
export const handleNav = () => {
  const drops = document.querySelectorAll('.has-drop');
  const navTabsBtns = document.querySelectorAll('.header__desktop-nav-tabs-btn');
  const navTabsPanels = document.querySelectorAll('.header__desktop-nav-tabs-panel');

  drops.forEach(drop => {
      const tabsControl = drop.querySelector('.header__desktop-nav-tabs-controls');
      const panel = drop.querySelector('.header__desktop-nav-tabs-content');
      const firstTabBtn = tabsControl.querySelector('.header__desktop-nav-tabs-btn');
      const firstTabPanel = panel.querySelector('.header__desktop-nav-tabs-panel');

      drop.addEventListener('mouseover', (e) => {
        const elem = e.target;

        if (elem?.classList.contains('header__desktop-nav-link')) {
          navTabsBtns.forEach((navTabsBtn) => navTabsBtn.classList.remove('active'));
          navTabsPanels.forEach((navTabsPanel) => navTabsPanel.classList.remove('active'));

          firstTabBtn.classList.add('active');
          firstTabPanel.classList.add('active');
        } else {
          drop.removeEventListener('mouseover', () => {});
        }
      });

      navTabsBtns.forEach((navTabsBtn) => {
        navTabsBtn.addEventListener('click', () => {
          navTabsBtns.forEach((navTabsBtn) => navTabsBtn.classList.remove('active'));

          navTabsPanels.forEach(item => {
            if (item.dataset.id === navTabsBtn.dataset.target) {
              navTabsBtn.classList.add('active');
              item.classList.add('active');

            } else {
              item.classList.remove('active');
            }
          });
        });
      });
  });

  return navTabsBtns;
}

