import CreateElement from "../helpers/createElement";
import createElement from "../helpers/createElement";
import AddClassNamesForHeaderMenu from "../helpers/addClassNamesForHeaderMenu";
import generateUniqueId from "generate-unique-id";

const Navigation = () => {
    const container = document.querySelector(".header__nav");

    if(container) {
        const navList = AddClassNamesForHeaderMenu();

        const desktopNav = CreateElement({
            className: "header__desktop-nav"
        });

        const desktopNavItems = navList.items.map((item, itemIndex) => {
            const navItem = createElement({
                className: ["header__desktop-nav-item", item.items.length > 0 ? "has-drop" : ""]
            });

            const navItemLink = createElement({
                type: "a",
                attributes: {
                    "href": item.link.getAttribute("href")
                },
                content: item.link.innerHTML,
                className: "header__desktop-nav-link"
            });
            navItem.appendChild(navItemLink);

            const navItemDrop = createElement({
                className: "header__desktop-nav-drop"
            });
            const tabsNavContainer = CreateElement({
                className: "header__desktop-nav-tabs-controls"
            });
            const tabsContent = CreateElement({
                className: "header__desktop-nav-tabs-content"
            });
            navItemDrop.appendChild(tabsNavContainer)
            navItemDrop.appendChild(tabsContent)

            item.items.forEach((subItem) => {
                const id = generateUniqueId();
                const tabsNavPanel = CreateElement({
                    className: "header__desktop-nav-tabs-panel",
                    attributes: {
                        "data-id": id,
                    }
                });
                const tabsNavOpener = CreateElement({
                    type: "a",
                    attributes: {
                        "data-target": id,
                        "href": subItem.link.getAttribute("href")
                    },
                    className: "header__desktop-nav-tabs-btn",
                    content: subItem.link.innerHTML
                });
                tabsNavContainer.appendChild(tabsNavOpener)
                const tabsContentItems = subItem.items.map((lastItem) => {
                    return  CreateElement({
                        type: "a",
                        attributes: {
                            "href": lastItem.link.getAttribute("href")
                        },
                        content: lastItem.link.innerHTML,
                        className: ["header__desktop-nav-tab-btn"]
                    });
                })
                tabsContentItems.forEach(tabsNavItem => tabsNavPanel.appendChild(tabsNavItem));
                tabsContent.appendChild(tabsNavPanel)
            })


            navItem.appendChild(navItemDrop);
            return navItem;
        })
        desktopNavItems.forEach(item => {
            desktopNav.appendChild(item)
        })

        const navContainer = document.querySelector(".header__mobile-nav");
        container.appendChild(desktopNav)

    }
}

export default Navigation;
