
export const footerMobileNav = () => {
  const navListItems = [...document.querySelectorAll('.footer-nav-list > li')];

  navListItems.forEach(navListItem => {
    navListItem.addEventListener('click', () => {
      const existedActiveItem = navListItems.filter(item => item.classList.contains('active'))[0];

      if (existedActiveItem !== navListItem) {
        existedActiveItem?.classList.remove('active');
      }

      navListItem.classList.toggle('active');
    });
  });
}
