const CreateElement = ({type, className, attributes, content}) => {
    const newEl = document.createElement(type || "div");

    if(className) {
        const createClassList = typeof(className) === "object" ? className.filter(str => str) : [className];
        newEl.classList.add(...createClassList);
    }

    if(attributes) {
        Object.keys(attributes).forEach(attr => {
            newEl.setAttribute(attr, attributes[attr])
        })
    }

    if(content) {
        newEl.innerHTML = content;
    }

    return newEl;
}

export default CreateElement;
