import Navigation from "./modules/navigation";
import {handleNav} from './modules/handleNav';
import {handleDropDownNav} from './modules/handleDropDownNav';
import {handleSlider} from './modules/slider';
import {accordion} from './modules/accordion';
import {footerMobileNav} from './modules/footerMobileNav';
import {scrollHandle} from './modules/scrollHandle';
import DescriptionSection from "./modules/description-section";
import {handleClipboard} from './modules/handleClipboard';
import {handleLangSwitch} from './modules/handleLangSwitch';

document.addEventListener("DOMContentLoaded", function () {
   Navigation();
   DescriptionSection();
   handleNav();
   handleDropDownNav();
   handleSlider();
   accordion();
   footerMobileNav();
   scrollHandle();
   handleClipboard();
   handleLangSwitch();

   const errorPageTemplate = document.querySelector(".error-page");
   if(errorPageTemplate) {
      errorPageTemplate.siblings(".page__main").classList.add("inner");
   }

   const asideActions = document.querySelector(".description-section__aside-actions");
   if(asideActions) {
      const container = document.querySelector(".description-section");
      container.classList.add("flex-container");
      asideActions.parentNode.classList.add("sticky");
   }
});

