export const handleDropDownNav = () => {
  const dropItems = [...document.querySelectorAll('.header__nav-drop-item')];
  const menuButton = document.querySelector('.header__menu-btn');
  const nav = document.querySelector('.header__nav');

  menuButton.addEventListener('click', () => {
    nav.classList.toggle('active');
    menuButton.classList.toggle('active');

    dropItems.forEach(item => item.classList.remove('active'));
  });

  dropItems.forEach(dropItem => {
    const dropLink = dropItem?.querySelector('.header__nav-drop-link');

    dropLink.addEventListener('click', () => {
      const existedActiveItem = dropItems.filter(item => item.classList.contains('active'))[0];

      if (existedActiveItem !== dropItem) {
        existedActiveItem?.classList.remove('active');
      }

      dropItem.classList.toggle('active');
    });
  });
}
