
const AddClassNamesForHeaderMenu = () => {
    const listContainer = document.querySelector(".header__mobile-nav > ul");
    if(listContainer) {
        listContainer.classList.add("header__nav-list");
    }

    const list = Array.from(document.querySelectorAll(".header__mobile-nav > ul > li"));
    list.forEach(item => item.classList.add("header__nav-item"));

    const listWithFirstLinks = Array.from(document.querySelectorAll(".header__mobile-nav > ul > li > a"));
    listWithFirstLinks.forEach(link => link.classList.add("header__nav-item-link"));

    const listWithDrops = Array.from(document.querySelectorAll(".header__mobile-nav > ul > li > ul"));
    listWithDrops.forEach(subList => subList.classList.add("header__nav-drop"));

    const listWithSubItems = Array.from(document.querySelectorAll(".header__mobile-nav > ul > li > ul > li"));
    listWithSubItems.forEach(item => item.classList.add("header__nav-drop-item"));

    const listWithSubItemsLink = Array.from(document.querySelectorAll(".header__mobile-nav > ul > li > ul > li > a"));
    listWithSubItemsLink.forEach(item => item.classList.add("header__nav-drop-link"));

    const listWithEndListInside = Array.from(document.querySelectorAll(".header__mobile-nav > ul > li > ul > li > ul"));
    listWithEndListInside.forEach(item => item.classList.add("header__nav-end"));

    const listWithEndItemsInside = Array.from(document.querySelectorAll(".header__mobile-nav > ul > li > ul > li > ul > li"));
    listWithEndItemsInside.forEach(item => item.classList.add("header__nav-end-item"));

    const listWithEndLinksInside = Array.from(document.querySelectorAll(".header__mobile-nav > ul > li > ul > li > ul > li > a"));
    listWithEndLinksInside.forEach(item => item.classList.add("header__nav-end-link"));

    const collection = {
        self: listContainer,
        items: list.map(el => {
            return {
                self: el,
                link: el.querySelector(".header__nav-item-link"),
                items: Array.from(el.querySelectorAll(".header__nav-drop-item")).map(subItem => {
                    return {
                        self: subItem,
                        link: subItem.querySelector(".header__nav-drop-link"),
                        items: Array.from(subItem.querySelectorAll(".header__nav-end-item")).map(endItem => {
                            return {
                                self: endItem,
                                link: endItem.querySelector(".header__nav-end-link"),
                            }
                        })
                    }
                })
            }
        })
    }

    return collection;
}

export default AddClassNamesForHeaderMenu;