
export const scrollHandle = () => {
  const scrollContainers = document.querySelectorAll('.scroll-container');

  scrollContainers.forEach(scrollContainer => {
    let isLocked = {
      minus: true,
      plus: true
    };
    scrollContainer.addEventListener("wheel", (event) => {
      const container = event.currentTarget;
      const indent = event.deltaY + event.deltaX;

      if((isLocked.minus && indent < 0) || (isLocked.plus && indent > 0)) {
        event.preventDefault();
      }

      if (!event.deltaY) return;

      event.currentTarget.scrollLeft += indent;
      const newScrollLeft = container.scrollLeft + event.deltaX;
      const maxScrollLeft = container.scrollWidth - container.clientWidth;
      const isScrollEnd = newScrollLeft === maxScrollLeft;
      const isScrollInitial = newScrollLeft !== 0;

      isLocked = {
        minus: isScrollInitial,
        plus: !isScrollEnd
      }

    }, {passive:false});
  });
}
