import copy from 'copy-to-clipboard';
export const handleClipboard = () => {
  const buttons = [...document.querySelectorAll('.combo-box__header-btn')];

  buttons.forEach(button => {
    button.addEventListener('click', async () => {
      const targetContent = button.querySelector('.btn-text');
      let copyText = targetContent.textContent;

      copy(copyText, {
        debug: true,
        message: 'Press #{key} to copy',
      });
    });
  });
}
