import createElement from "../helpers/createElement";
import generateUniqueId from "generate-unique-id";

const DescriptionSection = () => {
    const container = document.querySelector(".description-section");
    const navContainer = document.querySelector(".description-section__nav");

    if(container && container.querySelector(".offers--sticky")) {
        const pageContainer = document.querySelector("div.page");
        if(pageContainer) {
            pageContainer.classList.add("no-hidden")
        }
    }

    if(container && navContainer) {
        const pageContainer = document.querySelector("div.page");
        if(pageContainer) {
            pageContainer.classList.add("no-hidden")
        }
        const titles = [
            ...[...container.querySelectorAll("h2, h3")]
        ]
        const bodyOfTheNav = navContainer.querySelector(".description-section__nav-body")
        titles.forEach(title => {
            const text = title.innerText;
            const id = generateUniqueId();
            const anchor = createElement({
                type: "em",
                attributes: {id: id},
                className: "section-anchor"
            });
            title.appendChild(anchor)

            const newRow = createElement({
                className: "description-section__nav-row"
            });
            const navAnchor = createElement({
                type: "a",
                className: "description-section__nav-link",
                content: text,
                attributes: {
                    "href": "#" + id
                }
            })
            newRow.appendChild(navAnchor);
            bodyOfTheNav.appendChild(newRow);
        });

        navContainer.addEventListener("click", e => e.stopPropagation())
        const opener = navContainer.querySelector(".description-section__nav-heading");
        opener?.addEventListener("click", () => {
            const classList = navContainer.classList;
            classList.contains("active") ? classList.remove("active") : classList.add("active");
        });
        document.addEventListener("click", () => {
            navContainer.classList.remove("active");
        });
        window.addEventListener("scroll", () => {
            navContainer.classList.remove("active");
        })
    }
}

export default DescriptionSection;