
const addActiveClass = (elementLIst) => {
  elementLIst.forEach(dropDownItem => {
    dropDownItem.addEventListener('click', () => {
      elementLIst.forEach(item => item.classList.remove('active'));

      if (!dropDownItem.classList.contains('active')) {
        dropDownItem.classList.add('active');
      }
    });
  });
}


export const handleLangSwitch = () => {
  const dropDown = document.querySelector('.lang-switch__drop-down');
  const dropDownList = dropDown.querySelectorAll('.lang-switch__item');

  addActiveClass(dropDownList);
}
