
export const accordion = () => {
  const accordionItems = [...document.querySelectorAll('.accordion__item')];

  accordionItems.forEach((accordionItem) => {
    const header = accordionItem.querySelector('.accordion__header');

    header.addEventListener('click', () => {
      // const existedActiveItem = accordionItems.filter(item => item.classList.contains('active'))[0];
      //
      // if (existedActiveItem !== accordionItem) {
      //   existedActiveItem?.classList.remove('active');
      // }

      const classList = accordionItem.classList;
      classList.toggle('active');
    });
  });
}
